.loader, .global-loader {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, .7);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.loader div, .global-loader div {
  box-sizing: border-box;
  width: 64px;
  height: 64px;
  border: 8px solid rgba(0, 0, 0, 0);
  border-top-color: #ff6b01;
  border-radius: 50%;
  margin: 8px;
  animation: loading 1.2s cubic-bezier(.5, 0, .5, 1) infinite;
  display: block;
  position: absolute;
}

.loader div:nth-child(1), .global-loader div:nth-child(1) {
  animation-delay: -.45s;
}

.loader div:nth-child(2), .global-loader div:nth-child(2) {
  animation-delay: -.3s;
}

.loader div:nth-child(3), .global-loader div:nth-child(3) {
  animation-delay: -.15s;
}

@keyframes loading {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.global-loader {
  height: 100vh;
  width: 100vw;
  position: fixed;
}

